@font-face {
  font-weight: normal;
  font-family: "PT Sans";
  font-style: normal;
  font-display: swap;
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "PT Sans";
  font-style: normal;
  font-display: swap;
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  white-space: nowrap;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.page {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
.page__body {
  margin: 0;
  background-color: #f2f2f2;
}
.page__wrapper {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100vh;
  background-color: #ffffff;
  box-shadow: 0 5px 15px 0 rgba(1, 1, 1, 0.2);
  display: grid;
  grid-template-rows: auto 1fr auto;
}
.button {
  display: inline-block;
  width: 100%;
  padding: 15px 10px;
  font: inherit;
  font-size: 21px;
  line-height: 26px;
  font-weight: bold;
  vertical-align: middle;
  text-transform: uppercase;
  border: none;
  word-wrap: break-word;
  color: #ffffff;
  background-color: #81b3d2;
}
.button:hover,
.button:focus {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.page-header__intro {
